import React, { memo, useEffect, useRef, useState } from 'react';
import Downshift from 'downshift';
import { arrayOf, bool, func, shape, string } from 'prop-types';
import Listbox from './listbox';
import classnames from 'classnames';
import ButtonIcon from '../button-icon';
import Icon from '../icon';
import Input from '../input';
import useScrollToItem from './useScrollToItem';
import useComboboxAction from './useComboboxAction';
import { ComboboxUtil } from '@visa/ui-utils';

const CSS_PREFIX = 'vds-combobox';

const preventRerender = (prevProps, nextProps) => JSON.stringify(prevProps) === JSON.stringify(nextProps);

const Combobox = memo(({
  comboboxClass,
  containerProps,
  defaultItem,
  comboboxAction,
  css,
  errorText,
  filterFunction,
  inputProps,
  noMatchMessage,
  onInputBlur,
  onInputChange,
  onInputFocus,
  onSelectionChange,
  optionsTemplate,
  ...restProps
}) => {
  if (typeof filterFunction() !== 'boolean') {
    throw new Error('Filter function must return a boolean.');
  }

  const [currentSelectItem, setCurrentSelectItem] = useState(defaultItem);
  const inputRef = useRef(null);
  const [inputError, setInputError] = useState(errorText);
  const [isListboxOpen, setListboxOpen] = useState(false);
  const [searchHasNoMatch, setSearchHasNoMatch] = useState(false);
  const { listboxItemRef, listboxRef } = useScrollToItem(currentSelectItem, optionsTemplate);

  //Reverse of Control, expose downshift internal action to parent component
  const downshiftInternalActions = {};
  const actionList = ['clearSelection', 'closeMenu', 'openMenu', 'selectItem', 'setHighlightedIndex'];
  useComboboxAction(comboboxAction, downshiftInternalActions);

  useEffect(() => {
    if (errorText && errorText !== inputError) {
      // if noMatchMessage and errorText both exist, show noMatchMessage
      if (inputError && inputError === noMatchMessage) {
        return;
      }
      setInputError(errorText);
    } else if (!errorText && inputError && inputError !== noMatchMessage) {
      setInputError(undefined);
    }
  }, [errorText, inputError, noMatchMessage]);
  //if there is no match, show error message
  useEffect(() => {
    if (searchHasNoMatch && noMatchMessage) {
      setInputError(noMatchMessage);
    } else if (inputError && inputError === noMatchMessage) {
      //if errorText exist, when noMatch error disappear, reset back to errorText
      if (errorText) {
        setInputError(errorText);
      } else {
        setInputError(undefined);
      }
    }
    if (searchHasNoMatch) {
      setListboxOpen(false);
    } else {
      setListboxOpen(true);
    }
  }, [errorText, inputError, noMatchMessage, searchHasNoMatch]);

  //set default index
  let defaultIndex = -1;
  if (defaultItem && optionsTemplate && optionsTemplate.optionValues) {
    defaultIndex = optionsTemplate.optionValues.findIndex(
      item => item[optionsTemplate.itemKey] === defaultItem[optionsTemplate.itemKey]
    );
  }

  const handleInputBlur = e => {
    if (inputError && inputError === noMatchMessage) {
      setInputError(undefined);
    }
    if (typeof onInputBlur === 'function') {
      onInputBlur(e, e.target.value);
    }
  };

  const handleInputChange = e => {
    if (typeof onInputChange === 'function') {
      onInputChange(e, e.target.value);
    }
  };

  const handleInputFocus = e => {
    if (inputRef.current) {
      inputRef.current.setSelectionRange(0, e.target.value.length);
    }
    if (typeof onInputFocus === 'function') {
      onInputFocus(e, e.target.value);
    }
  };

  const handleSelection = selection => {
    setCurrentSelectItem(selection ? selection : null);
    setInputError(undefined);
    if (typeof onSelectionChange === 'function') {
      onSelectionChange(selection);
    }
  };

  const handleNoMatch = noMatch => {
    if (searchHasNoMatch !== noMatch) {
      setSearchHasNoMatch(noMatch);
    }
  };

  const stateReducer = (state, changes) => {
    switch (changes.type) {
      case Downshift.stateChangeTypes.clickButton:
        if (state.isOpen) {
          let newChanges = Object.assign({}, changes);
          if (state.inputValue) {
            if (!state.selectedItem) {
              newChanges = Object.assign({}, newChanges, { inputValue: '' });
            } else if (state.selectedItem && state.selectedItem.value !== state.inputValue) {
              newChanges = Object.assign({}, newChanges, { inputValue: state.selectedItem.value });
            }
          } else if (!state.inputValue && state.selectedItem) {
            newChanges = Object.assign({}, newChanges, { inputValue: state.selectedItem.value });
          }
          return newChanges;
        }
        return changes;
      default:
        return changes;
    }
  };

  return (
    <Downshift
      defaultHighlightedIndex={defaultIndex}
      initialInputValue={defaultItem ? defaultItem.value : ''}
      initialSelectedItem={currentSelectItem}
      itemToString={item => (item ? item.value : '')}
      onChange={handleSelection}
      stateReducer={stateReducer}
      getA11yStatusMessage={status => (status.isOpen && status.highlightedItem ? status.highlightedItem.value : '')}
    >
      {({ getInputProps, getToggleButtonProps, isOpen, ...listboxDownshift }) => {
        if (Object.keys(downshiftInternalActions).length === 0) {
          downshiftInternalActions.setInputError = setInputError;
          for (const action of actionList) { // eslint-disable-line
            downshiftInternalActions[action] = listboxDownshift[action];
          }
        }
        return (
          <div
            {...containerProps}
            className={classnames(`${CSS_PREFIX}-container`, comboboxClass.containerClass)}
            css={css}
          >
            <Input
              {...getInputProps({
                onBlur: handleInputBlur,
                onChange: handleInputChange,
                onFocus: handleInputFocus
              })}
              {...inputProps}
              className={classnames(`${CSS_PREFIX}-input-wrapper`, comboboxClass.inputWrapperClass)}
              errorText={inputError}
              invalid={Boolean(inputError)}
              labelId={getInputProps()['aria-labelledby']}
              ref={inputRef}
              showErrorText={Boolean(inputError)}
              showHelpText={Boolean(inputProps.helpText)}
              type="text"
            >
              <ButtonIcon
                ariaLabel={isOpen ? 'collapse' : 'expand'}
                {...getToggleButtonProps({})}
                iconType="light-tiny"
                tabIndex="-1"
              >
                <Icon name={isOpen ? 'arrow-collapse' : 'arrow-expand'} resolution="low" />
              </ButtonIcon>
            </Input>
            <Listbox
              {...listboxDownshift}
              {...restProps}
              filterFunction={filterFunction}
              listboxClass={comboboxClass.listboxClass}
              isListboxOpen={isListboxOpen}
              listboxItemRef={li => {
                listboxItemRef.current = li;
              }}
              listboxRef={ul => {
                listboxRef.current = ul;
              }}
              isOpen={isOpen}
              noMatchCallback={handleNoMatch}
              optionsTemplate={optionsTemplate}
            />
          </div>
        );
      }}
    </Downshift>
  );
}, preventRerender);

Combobox.defaultProps = {
  comboboxClass: {
    containerClass: null,
    inputWrapperClass: null,
    listboxClass: null
  },
  errorText: '',
  filterFunction: ComboboxUtil.filterFunction,
  inputProps: {
    label: 'Label',
    name: 'combobox'
  },
  noMatchMessage: 'No entries matching, Please try again.'
};

Combobox.propTypes = {
  comboboxAction: func,
  comboboxClass: shape({
    containerClass: string,
    inputWrapperClass: string,
    listboxClass: string
  }),
  containerProps: shape({}),
  css: func,
  defaultItem: shape({
    value: string.isRequired
  }),
  errorText: string,
  filterFunction: func,
  inputProps: shape({
    disabled: bool,
    helpText: string,
    label: string.isRequired,
    name: string.isRequired,
    placeholder: string,
    required: bool
  }),
  noMatchMessage: string,
  onInputBlur: func,
  onInputChange: func,
  onInputFocus: func,
  onSelectionChange: func,
  optionsTemplate: shape({
    itemKey: string.isRequired,
    optionValues: arrayOf(shape({ value: string.isRequired })),
    renderOption: func.isRequired,
    searchIncludeFields: arrayOf(string)
  }).isRequired
};

Combobox.displayName = 'Combobox';

export default Combobox;
